import { createSlice } from "@reduxjs/toolkit"

// useState .
// state  slice .
const user = createSlice({
    name : 'user',          // state 
    // initialState : 'kim',   // 
    initialState : { name : 'kim' , age : 20 },   // Object, Array 
    reducers : {
        // 1. state   
        changeName(state) { // state  state .
            // return 'john' + state
            // return { name : 'park' , age : 20 }   // Object, Array  
            state.name = 'park'                      // Object, Array  
        },
        increase(state, action) {
          state.age += action.payload
        },
    }    
})

// state  .
// 2.   export .
export const { changeName, increase } = user.actions

export default user